import React from 'react';
import Modal from '../../components/Modal';
import ModalHeading from '../../components/Modal/ModalHeading';
import ModalForm from '../../components/Modal/ModalForm';
import InputsSection from '../../components/Modal/InputsSection';
import InputsSectionColumn from '../../components/Modal/InputsSectionColumn';
import InputBox from '../../components/formInputs/InputBox';
import ModalButton from '../../components/Modal/ModalButton';
import FileUpload from '../../components/formInputs/FileUpload';
import TextareaBox from '../../components/formInputs/TextareaBox';

const EditProfile = ({ data }) => {
	const {
		name,
		email,
		address,
		phone,
		setName,
		setEmail,
		setPhone,
		setAddress,
		sessionCharges,
		setSessionCharges,
		sessionChargesError,
		setSessionChargesError,

		nameError,
		addressError,
		phoneError,
		emailError,

		setNameError,
		setAddressError,
		setPhoneError,
		setEmailError,

		imageSrc,
		setImageSrc,
		imageSrcError,
		setImageSrcError,
		setUploadFileData,
		uploadFileDataError,
		setUploadFileDataError,

		handleEditModalClose,
		handleSubmit,
		showEditModal,
		setShowEditModal,
		selectUpdateModel,
		setSelectUpdateModel
	} = data;

	const handleChangeImage = (e) => {
		var file = e.target.files[0];
		var reader = new FileReader();
		if (e.target.files[0]) {
			var url = reader.readAsDataURL(file);
			reader.onloadend = () => {
				setImageSrc(reader.result);
			};
		}
	}

	return (
		<Modal show={showEditModal}>
			<ModalHeading heading='Update Profile' onClose={handleEditModalClose} />
			<ModalForm onSubmit={handleSubmit} style={{ marginBottom: '2.5rem' }}>
				<InputsSection>
					<InputBox
						label='Name'
						icon='gym'
						placeholder='eg, Gold Salon'
						value={name}
						onChange={(event) => {
							setName(event.target.value);
							setNameError('');
						}}
						errorMessage={nameError}
					/>
					<InputBox
						label='Email'
						icon='email'
						placeholder='eg, johndoe@example.com'
						value={email}
						onChange={(event) => {
							setEmail(event.target.value);
							setEmailError('');
						}}
						errorMessage={emailError}
					/>
					<InputBox
						label='Phone'
						icon='phone'
						placeholder='eg, 123 456 7890'
						value={phone}
						onChange={(event) => {
							setPhone(event.target.value);
							setPhoneError('');
						}}
						errorMessage={phoneError}

					/>
				</InputsSection>
				<InputsSectionColumn>
					<div className='flex' style={{ gap: '2rem' }}>

						<TextareaBox
							label='Address'
							icon='pin'
							placeholder='eg, Devar, USA'
							value={address}
							onChange={(event) => {
								setAddress(event.target.value);
								setAddressError('');
							}}
							errorMessage={addressError}

						/>

						{
							selectUpdateModel &&
							(selectUpdateModel.role === 'trainer' || selectUpdateModel.role === 'gym') &&
							<InputBox
								label='Session Charges'
								icon='cash'
								placeholder='eg, $ 00.00'
								value={sessionCharges}
								onChange={(event) => {
									setSessionCharges(event.target.value);
									setSessionChargesError('');
								}}
								errorMessage={sessionChargesError}

							/>

						}
					</div>
				</InputsSectionColumn>
				<FileUpload
					label='Upload Salon Logo'
					icon='upload'
					image={imageSrc}
					onChange={(e) => {
						handleChangeImage(e);
						setUploadFileData(e.target.files);
						setUploadFileDataError('');
					}}
					errorMessage={uploadFileDataError}
				/>
				<ModalButton label='Update Profile' icon='edit' onClick={handleSubmit} />
			</ModalForm>
		</Modal>
	);
};

export default EditProfile;
