import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import Content from '../../components/Content';
import Spinner from '../../components/Spinner/Spinner';
import {
	changeStatusStylerAction,
	createNewStylerAction,
	deleteStylerAction,
	getStylerListAction
} from '../../redux/actions/stylerActions';
import { validateEmail, validatePhone } from '../../utils/validators';
import { getAllEnableCompanyAction } from '../../redux/actions/companyActions';
import { changePasswordUserAction } from '../../redux/actions/userActions';
import { CHANGE_PASSWORD_USER_RESET } from '../../redux/constants/userConstants';
import {
	STYLER_RESET,
	CHANGE_STATUS_STYLER_RESET,
	CREATE_NEW_STYLER_RESET,
	DELETE_STYLER_RESET
} from '../../redux/constants/stylerConstants';
import CreateStyler from './CreateStyler';
import DeleteStyler from './DeleteStyler';
import StylerData from './StylerData';
import ChangePwdUser from '../UsersScreen/ChangePwdUser';
import FormScreen2 from '../DeclarationForms/FormScreen2';
import {
	CREATE_NEW_INDEPENT_AGREEMENT_RESET,
	GET_NEW_INDEPENT_AGREEMENT_RESET
} from '../../redux/constants/formConstants';
import {
	createIndepentAgreementByStylerAction,
	getFormByIndependentAgreementAction
} from '../../redux/actions/formActions';
import Pagination from '../../components/Pagination';
import { GET_ALL_ENABLE_COMPANY_RESET } from '../../redux/constants/companyConstants';

const StylersScreen = ({ showFilter = false, history }) => {
	const dispatch = useDispatch();
	const userInfo = useSelector((state) => state.userLogin);
	const getUserInfo = useSelector((state) => state.getUserInfo);

	const trainerList = useSelector((state) => state.trainerList);
	const companyList = useSelector((state) => state.enableComanyList);

	const createStyler = useSelector((state) => state.createStyler);
	const changeStatusStyler = useSelector((state) => state.changeStatusStyler);
	const deleteStyler = useSelector((state) => state.deleteStyler);
	const changePasswordUser = useSelector((state) => state.changePasswordUser);
	const createFormByIndependentAgreement = useSelector((state) => state.createFormByIndependentAgreement);
	const getFormByIndependentAgreement = useSelector((state) => state.getFormByIndependentAgreement);

	const [ uploadFileData, setUploadFileData ] = useState('');
	const [ uploadFileDataError, setUploadFileDataError ] = useState('');

	const [ showAddModal, setShowAddModal ] = useState(false);
	const [ modalDeleteState, setModalDeleteState ] = useState(false);
	const [ modalDoumentState, setModalDoumentState ] = useState(false);
	const [ modalChangePasswordState, setModalChangePasswordState ] = useState(false);

	const [ selectUpdateModel, setSelectUpdateModel ] = useState({});
	const [ totalPageSize, setTotalPageSize ] = useState(1);
	const [ pageNumber, setPageNumber ] = useState(1);
	const pageLimit = 20;

	const [ id, setUserId ] = useState('');
	const [ name, setName ] = useState('');
	const [ nameError, setNameError ] = useState('');
	const [ email, setEmail ] = useState('');
	const [ emailError, setEmailError ] = useState('');
	const [ password, setPassword ] = useState('');
	const [ passwordError, setPasswordError ] = useState('');
	const [ address, setAddress ] = useState('');
	const [ addressError, setAddressError ] = useState('');
	const [ phone, setPhone ] = useState('');
	const [ phoneError, setPhoneError ] = useState('');
	const [ imageSrc, setImageSrc ] = useState('');
	const [ imageSrcError, setImageSrcError ] = useState('');
	const [ search, setSearch ] = useState('');

	const [ company, setCompany ] = useState('');
	const [ companyError, setCompanyError ] = useState('');

	const [ trainerSignature, setStylerSignature ] = useState('');

	const [ gymSignature, setSalonSignature ] = useState('');
	const [ gymSignatureSrc, setSalonSignatureSrc ] = useState('');

	const [ trainerSignatureDate, setStylerSignatureDate ] = useState('');
	const [ gymSignatureDate, setSalonSignatureDate ] = useState('');
	const [ gymSignatureError, setSalonSignatureError ] = useState('');
	const [ gymSignatureDateError, setSalonSignatureDateError ] = useState('');

	useEffect(
		() => {
			if (
				getUserInfo &&
				getUserInfo.userInfo &&
				getUserInfo.userInfo.data &&
				(getUserInfo.userInfo.data.role === 'gym' ||
					getUserInfo.userInfo.data.role === 'gymadmin' ||
					getUserInfo.userInfo.data.role === 'admin' ||
					getUserInfo.userInfo.data.role === 'superadmin')
			) {
				dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: '' }));
				dispatch(getAllEnableCompanyAction({ pageNumber: 1, pageSize: 100, filter: '' }));

				return () => {
					dispatch({ type: STYLER_RESET });
					dispatch({ type: GET_ALL_ENABLE_COMPANY_RESET });
				};
			} else {
				history.push('/');
			}
		},
		[ getUserInfo, dispatch ]
	);

	useEffect(
		() => {
			if (createStyler && createStyler.userInfo && createStyler.userInfo.status) {
				setSearch('');
				clearData();
				setShowAddModal(false);
				dispatch({ type: CREATE_NEW_STYLER_RESET });
				toast.success(createStyler.userInfo.message);
				dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: '' }));
			} else if (createStyler && createStyler.userInfo && !createStyler.userInfo.status) {
				dispatch({ type: CREATE_NEW_STYLER_RESET });
				toast.error(createStyler.userInfo.message);
			}
		},
		// eslint-disable-next-line
		[ createStyler, dispatch ]
	);

	useEffect(
		() => {
			if (deleteStyler && deleteStyler.userInfo && deleteStyler.userInfo.status) {
				toast.success(deleteStyler.userInfo.message);
				dispatch({ type: DELETE_STYLER_RESET });
				dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: '' }));
			} else if (deleteStyler && deleteStyler.userInfo && !deleteStyler.userInfo.status) {
				toast.error(deleteStyler.userInfo.message);
				dispatch({ type: DELETE_STYLER_RESET });
				dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: '' }));
			}
		},
		// eslint-disable-next-line
		[ deleteStyler, dispatch ]
	);

	useEffect(
		() => {
			if (changeStatusStyler && changeStatusStyler.userInfo && changeStatusStyler.userInfo.status) {
				toast.success(changeStatusStyler.userInfo.message);
				setSelectUpdateModel({});
				dispatch({ type: CHANGE_STATUS_STYLER_RESET });
				dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: '' }));
			} else if (changeStatusStyler && changeStatusStyler.userInfo && !changeStatusStyler.userInfo.status) {
				toast.error(changeStatusStyler.userInfo.message);
				dispatch({ type: CHANGE_STATUS_STYLER_RESET });
				dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: '' }));
			}
		},
		// eslint-disable-next-line
		[ changeStatusStyler, dispatch ]
	);

	useEffect(
		() => {
			if (changePasswordUser && changePasswordUser.userInfo && changePasswordUser.userInfo.status) {
				toast.success(changePasswordUser.userInfo.message);
				setSelectUpdateModel({});
				dispatch({ type: CHANGE_PASSWORD_USER_RESET });
			} else if (changePasswordUser && changePasswordUser.userInfo && !changePasswordUser.userInfo.status) {
				toast.error(changePasswordUser.userInfo.message);
				dispatch({ type: CHANGE_PASSWORD_USER_RESET });
			}
		},
		// eslint-disable-next-line
		[ changePasswordUser, dispatch ]
	);

	useEffect(
		() => {
			if (
				createFormByIndependentAgreement &&
				createFormByIndependentAgreement.userInfo &&
				createFormByIndependentAgreement.userInfo.status
			) {
				clearData();
				dispatch({ type: CREATE_NEW_INDEPENT_AGREEMENT_RESET });
				toast.success(createFormByIndependentAgreement.userInfo.message);
			} else if (
				createFormByIndependentAgreement &&
				createFormByIndependentAgreement.userInfo &&
				!createFormByIndependentAgreement.userInfo.status
			) {
				dispatch({ type: CREATE_NEW_INDEPENT_AGREEMENT_RESET });
				toast.error(createFormByIndependentAgreement.userInfo.message);
			}
		},
		// eslint-disable-next-line
		[ createFormByIndependentAgreement, dispatch ]
	);

	useEffect(
		() => {
			dispatch(getFormByIndependentAgreementAction(selectUpdateModel._id));

			return () => {
				dispatch({ type: GET_NEW_INDEPENT_AGREEMENT_RESET });
			};
		},
		[ selectUpdateModel, dispatch ]
	);

	useEffect(
		() => {
			if (
				getFormByIndependentAgreement &&
				getFormByIndependentAgreement.userInfo &&
				getFormByIndependentAgreement.userInfo.data
			) {
				setSalonSignatureSrc(getFormByIndependentAgreement.userInfo.data.gymSignature);
				setSalonSignatureDate(getFormByIndependentAgreement.userInfo.data.gymSignatureDate);
				setStylerSignature(getFormByIndependentAgreement.userInfo.data.trainerSignature);
				setStylerSignatureDate(getFormByIndependentAgreement.userInfo.data.trainerSignatureDate);
			}
		},
		[ getFormByIndependentAgreement, dispatch ]
	);

	const handleAddModalClose = () => {
		clearData();
		setShowAddModal(false);
	};

	const handleSubmit = (e) => {
		e.preventDefault();

		if (name === '' && name.trim() === '') {
			setNameError('Please enter name');
		} else if (email === '' && email.trim() === '') {
			setEmailError('Please enter email address');
		} else if (!validateEmail(email)) {
			setEmailError('Please enter valid email address');
		} else if (phone === '' && phone.trim() === '') {
			setPhoneError('Please enter phone');
		} else if (!validatePhone(phone)) {
			setPhoneError('Phone number must be 10 digits');
		} else if (address === '' && address.trim() === '') {
			setAddressError('Please enter address');
		} else if (id === '' && password === '') {
			setPasswordError('Please enter password');
		} else if (id === '' && password.length < 7) {
			setPasswordError('Password must be at least 8 characters');
		} else if (!imageSrc) {
			setUploadFileDataError('Please select files to upload');
		} else if (company === '' && company.trim() === '') {
			setCompanyError('Please select company');
		} else {
			let fileData = new FormData();
			if (id !== '') {
				fileData.append('id', id);
			} else {
				fileData.append('password', password);
			}

			fileData.append('name', name);
			fileData.append('email', email);
			fileData.append('address', address);
			fileData.append('phone', phone);
			fileData.append('company', company);
			for (let i = 0; i < uploadFileData.length; i++) {
				fileData.append('image', uploadFileData[i]);
			}

			dispatch(createNewStylerAction(fileData));
		}
	};

	const onDeleteModalClose = () => {
		setSelectUpdateModel({});
		setModalDeleteState(false);
	};

	const onDeleteHandler = (d) => {
		dispatch(deleteStylerAction(selectUpdateModel._id));
		dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: '' }));
		onDeleteModalClose();
	};

	const handlePageChange = async (currentPage) => {
		const selectedPage = currentPage.selected;
		dispatch(getStylerListAction({ pageNumber: selectedPage + 1, pageSize: pageLimit, filter: '' }));
		setPageNumber(selectedPage + 1);
	};

	const onChangeHandler = (d) => {
		dispatch(
			changeStatusStylerAction(d._id, {
				enable: d.active ? true : false
			})
		);
	};

	const clearData = () => {
		setSelectUpdateModel({});
		setSalonSignatureSrc('');
		setSalonSignatureDate('');
		setUserId('');
		setEmail('');
		setPassword('');
		setPhone('');
		setAddress('');
		setCompany('');
		setName('');
		setEmailError('');
		setPasswordError('');
		setPhoneError('');
		setAddressError('');
		setNameError('');
		setImageSrc('');
		setImageSrcError('');
		setCompanyError('');
		if (gymSignature) {
			gymSignature.clear();
		}
	};

	const handleEditModalStyler = (d) => {
		setUserId(d._id);
		setEmail(d.email);
		setPhone(d.phone);
		setName(d.name);
		setAddress(d.address);
		setImageSrc(d.photo);
		setCompany(d.company);
		setShowAddModal(true);
	};

	const onChangePwdUserHandler = () => {
		let d = selectUpdateModel;
		let type = 'byAdmin';
		dispatch(changePasswordUserAction({ id: d._id, type, email: d.email }));
		onChangePwdModalClose();
	};

	const onChangePwdModalClose = () => {
		setModalChangePasswordState(false);
	};

	const onFormModalClose = () => {
		setModalDoumentState(false);
	};

	const onFormHandler = (e) => {
		e.preventDefault();
		if (gymSignature === '' && gymSignature.trim() === '') {
			setSalonSignatureError('Please select signature date');
		} else if (gymSignatureDate === '' && gymSignatureDate.trim() === '') {
			setSalonSignatureDateError('Please select signature date');
		} else if (selectUpdateModel) {
			let base64 = '';
			if (!gymSignatureSrc) {
				base64 = gymSignature.getTrimmedCanvas().toDataURL('image/png');
			} else {
				base64 = gymSignatureSrc;
			}
			dispatch(
				createIndepentAgreementByStylerAction({
					gymSignature: base64,
					trainer: selectUpdateModel._id,
					gymSignatureDate
				})
			);
			onFormModalClose();
		}
	};

	const ClearHandler = (e) => {
		e.preventDefault();
		if (trainerSignature !== '' && trainerSignature.trim() !== '') {
			setStylerSignature('');
			setStylerSignatureDate('');
		}
	};

	const onSearchHandler = (event) => {
		setSearch(event.target.value);
		if (search.trim !== '' && search.length > 0) {
			setPageNumber(1);
		}
		dispatch(getStylerListAction({ pageNumber: pageNumber, pageSize: pageLimit, filter: event.target.value }));
	};

	return (
		<Content
			currentMenu='styler'
			headerTitle='Styler'
			addBtn={
				getUserInfo &&
				getUserInfo.userInfo &&
				getUserInfo.userInfo.data &&
				(getUserInfo.userInfo.data.role === 'gym' ||
					getUserInfo.userInfo.data.role === 'gymadmin' ||
					getUserInfo.userInfo.data.role === 'superadmin') ? (
					true
				) : (
					false
				)
			}
			addBtnText='Add New Styler'
			addBtnIcon='plus'
			addBtnClick={() => setShowAddModal(true)}
			search={true}
			searchPlaceholder='Search Stylers...'
			searchIcon='search'
			searchvalue={search}
			searchOnChange={onSearchHandler}>
			{trainerList.loading ? (
				<Spinner />
			) : trainerList &&
			trainerList.userInfo &&
			trainerList.userInfo.status &&
			trainerList.userInfo.data.result &&
			trainerList.userInfo.data.result.length > 0 ? (
				<div>
					<StylerData
						data={trainerList.userInfo.data.result}
						showFilter={showFilter}
						setShowAddModal={setShowAddModal}
						setModalDeleteState={setModalDeleteState}
						setSelectUpdateModel={setSelectUpdateModel}
						handleEditModalStyler={handleEditModalStyler}
						onChangeHandler={onChangeHandler}
						setModalChangePasswordState={setModalChangePasswordState}
						setModalDoumentState={setModalDoumentState}
					/>
					{totalPageSize > 1 && (
						<div className='tableContainer--paginater'>
							<Pagination
								list={trainerList.userInfo}
								onPageChange={handlePageChange}
								rowsPerPage={pageLimit}
								totalPageSize={totalPageSize}
								pageNumber={pageNumber}
							/>
						</div>
					)}
				</div>
			) : (
				<div className='not_data_found'>
					<h1>No data found !</h1>
				</div>
			)}

			<CreateStyler
				data={{
					name,
					email,
					password,
					address,
					phone,

					setUserId,
					setEmail,
					setPhone,
					setName,
					setAddress,
					setPassword,

					emailError,
					phoneError,
					nameError,
					passwordError,
					addressError,

					setEmailError,
					setPhoneError,
					setNameError,
					setAddressError,
					setPasswordError,

					companyList,
					company,
					setCompany,
					companyError,

					showAddModal,
					selectUpdateModel,
					handleAddModalClose,
					handleSubmit,

					imageSrc,
					setImageSrc,
					imageSrcError,
					setImageSrcError,
					setUploadFileData,
					uploadFileDataError,
					setUploadFileDataError
				}}
			/>
			<DeleteStyler data={{ modalDeleteState, onDeleteModalClose, onDeleteHandler }} />
			<ChangePwdUser data={{ modalChangePasswordState, onChangePwdModalClose, onChangePwdUserHandler }} />
			<FormScreen2
				data={{
					gymSignature,
					gymSignatureDate,
					gymSignatureDateError,
					gymSignatureSrc,
					setSalonSignatureSrc,
					setSalonSignature,
					setSalonSignatureDate,
					setSalonSignatureDateError,

					trainerSignature,
					trainerSignatureDate,

					createFormByIndependentAgreement,
					selectUpdateModel,
					modalDoumentState,
					onFormModalClose,

					onFormHandler
				}}
			/>
		</Content>
	);
};

export default StylersScreen;

{
	/* <ChangePassword data={{
				selectUpdateModel,
				setSelectUpdateModel,
				modalChangePasswordState,
				setModalChangePasswordState,
				// onChangePwdModalClose
			}} /> */
}
