import React, { useState, useEffect, Fragment } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import Content from '../../components/Content';
import { addStylerSessionAction, getStylerSessionsAction } from '../../redux/actions/attendanceActions';
import { ADD_STYLER_SESSION_RESET, GET_STYLER_SESSIONS_RESET } from '../../redux/constants/attendanceConstants';
import { validateOnlyNumberDecimal } from '../../utils/validators';

import { months, days } from './calendarData';
import StylerSessionStyles from './StylerSession.module.css';

const StylerSessionScreen = ({ history, match }) => {
	const dispatch = useDispatch();

	const [ loading, setLoading ] = useState(true);
	const [ currentMonthNumber, setCurrentMonthNumber ] = useState(null);
	const [ currentMonth, setCurrentMonth ] = useState({});
	const [ currentMonthDates, setCurrentMonthDates ] = useState([]);
	const [ currentYear, setCurrentYear ] = useState('');
	const [ currentDate, setCurrentDate ] = useState('');
	const [ currentDayName, setCurrentDayName ] = useState('');
	const [ currentMonthStartDay, setCurrentMonthStartDay ] = useState('');
	const [ currentMonthStartDayMargin, setCurrentMonthStartDayMargin ] = useState(0);
	const [ yesterdayDate, setYesterdayDate ] = useState({});
	const [ dayBeforeYesterdayDate, setDayBeforeYesterdayDate ] = useState({});
	const [ sessionBoxVisible, setSessionBoxVisible ] = useState({ visible: false, current: '' });
	const [ sessionText, setSessionText ] = useState({ value: '', error: '' });

	const userLogin = useSelector((state) => state.userLogin);
	const getStylerSessions = useSelector((state) => state.getStylerSessions);
	const addStylerSession = useSelector((state) => state.addStylerSession);
	const getUserInfo = useSelector((state) => state.getUserInfo);

	useEffect(
		() => {
			if (userLogin && userLogin.userInfo && userLogin.userInfo.data && !userLogin.userInfo.data.token) {
				history.push('/login');
			}
		},
		[ userLogin, history ]
	);

	useEffect(
		() => {
			if (currentMonthNumber) {
				if (match.params.trainerId) {
					dispatch(getStylerSessionsAction(currentMonthNumber + 1, match.params.trainerId));
				} else {
					dispatch(getStylerSessionsAction(currentMonthNumber + 1));
				}
			}
			setSessionBoxVisible({ visible: false, current: '' });
			setSessionText({ value: '', error: '' });

			return () => {
				dispatch({ type: GET_STYLER_SESSIONS_RESET });
			};
		},
		[ dispatch, currentMonthNumber, match ]
	);

	useEffect(() => {
		const date = new Date(Date.now());

		setCurrentYear(date.getFullYear());
		setCurrentMonthNumber(date.getMonth());

		const yesterday = new Date(new Date().setDate(new Date().getDate() - 1));
		const dayBeforeYesterday = new Date(new Date().setDate(new Date().getDate() - 2));
		setYesterdayDate({ date: yesterday.getDate(), month: yesterday.getMonth(), year: yesterday.getFullYear() });
		setDayBeforeYesterdayDate({
			date: dayBeforeYesterday.getDate(),
			month: dayBeforeYesterday.getMonth(),
			year: dayBeforeYesterday.getFullYear()
		});
	}, []);

	useEffect(
		() => {
			const date = new Date(Date.now());

			months.map((month, i) => i === currentMonthNumber && setCurrentMonth(month));
			setCurrentDate(date.getDate());
			// setCurrentDay(date.getDay());
			days.map((day, i) => i === date.getDay() && setCurrentDayName(day));

			setCurrentMonthStartDay(new Date(currentYear, currentMonthNumber, 1).getDay());
			setCurrentMonthStartDayMargin(currentMonthStartDay * 13.6);
		},
		[ currentMonthNumber, currentYear, currentMonthStartDay ]
	);

	useEffect(
		() => {
			const days = [];

			if (currentYear / 4 === 0 && currentMonth && currentMonth.name === 'February') {
				setCurrentMonth({ ...currentMonth, days: 29 });
			}
			if (currentMonth && currentMonth.days) {
				for (let i = 1; i <= currentMonth.days; i++) {
					days.push({ date: i, session: 0 });
				}
			}
			const days3 =
				getStylerSessions &&
				getStylerSessions.sessions &&
				getStylerSessions.sessions.data &&
				getStylerSessions.sessions.data.result &&
				getStylerSessions.sessions.data.result.length > 0 &&
				getStylerSessions.sessions.data.result;

			setLoading(getStylerSessions && getStylerSessions.loading);

			if (days && days.length > 0 && days3 && days3.length > 0) {
				for (let i = days.length - 1; i >= 0; i--) {
					for (let j = days3.length - 1; j >= 0; j--) {
						if (days[i].date === days3[j].date) {
							days[i] = days3[j];
						}
					}
				}
			}

			setCurrentMonthDates(days);
		},
		[ currentYear, currentMonth, getStylerSessions ]
	);

	useEffect(
		() => {
			if (addStylerSession && addStylerSession.session && addStylerSession.session.status) {
				setSessionBoxVisible({ visible: false, current: '' });
				setSessionText({ value: '', error: '' });
				toast.success(addStylerSession.session.message);
				if (currentMonthNumber) {
					if (match.params.trainerId) {
						dispatch(getStylerSessionsAction(currentMonthNumber + 1, match.params.trainerId));
					} else {
						dispatch(getStylerSessionsAction(currentMonthNumber + 1));
					}
				}
				dispatch({ type: ADD_STYLER_SESSION_RESET });
			} else if (addStylerSession && addStylerSession.session && !addStylerSession.session.status) {
				toast.success(addStylerSession.session.message);
				dispatch({ type: ADD_STYLER_SESSION_RESET });
			}
		},
		// eslint-disable-next-line
		[ addStylerSession, dispatch, currentMonthNumber, match ]
	);

	const handlePreviousMonth = () => {
		if (currentMonthNumber === 0) {
			setCurrentMonthNumber(11);
			setCurrentYear(currentYear - 1);
		} else setCurrentMonthNumber(currentMonthNumber - 1);
	};
	const handleNextMonth = () => {
		if (currentMonthNumber === 11) {
			setCurrentMonthNumber(0);
			setCurrentYear(currentYear + 1);
		} else setCurrentMonthNumber(currentMonthNumber + 1);
	};

	const handleAddSession = (current, sessionValue) => {
		if (
			(current === dayBeforeYesterdayDate.date &&
				currentMonthNumber === dayBeforeYesterdayDate.month &&
				currentYear === dayBeforeYesterdayDate.year) ||
			(current === yesterdayDate.date &&
				currentMonthNumber === yesterdayDate.month &&
				currentYear === yesterdayDate.year) ||
			(current === currentDate &&
				currentMonthNumber === new Date(Date.now()).getMonth() &&
				currentYear === new Date(Date.now()).getFullYear())
		) {
			setSessionBoxVisible({ visible: !sessionBoxVisible.visible, current });
			setSessionText({ value: sessionValue, error: '' });
		} else if (
			getUserInfo &&
			getUserInfo.userInfo &&
			getUserInfo.userInfo.data &&
			getUserInfo.userInfo.data.role === 'gym'
		) {
			setSessionBoxVisible({ visible: !sessionBoxVisible.visible, current });
			setSessionText({ value: sessionValue, error: '' });
		} else return;
	};

	const addSession = (date, days) => {
		if (sessionText.value === '') {
			setSessionText({ ...sessionText, error: 'Please enter session value' });
		} else if (!validateOnlyNumberDecimal(sessionText.value)) {
			setSessionText({ ...sessionText, error: 'Please enter valid session' });
		} else {
			const formData = {
				sessionDate: `${currentMonthNumber + 1}/${date}/${currentYear}`,
				session: sessionText.value
			};

			if (days._id) formData.id = days._id;
			if (match.params.trainerId) formData.trainer = match.params.trainerId;

			dispatch(addStylerSessionAction(formData));
		}
	};

	return (
		<Content currentMenu={`${match.params.trainerId ? 'styler' : 'trainer-sessions'}`} headerTitle='Sessions'>
			<div className={StylerSessionStyles.month}>
				<div className={StylerSessionStyles.headers}>
					<button className={StylerSessionStyles.prevNextBtn} onClick={handlePreviousMonth}>
						&#10094;
					</button>
					<p className={StylerSessionStyles.dayMonth}>
						{currentDayName + ' ' + currentDate}
						<span>
							{currentMonth && currentMonth.name && currentMonth.name} {currentYear}
						</span>
					</p>
					<button className={StylerSessionStyles.prevNextBtn} onClick={handleNextMonth}>
						&#10095;
					</button>
				</div>
				<ul className={StylerSessionStyles.weekdays}>
					{days && days.map((day, index) => <li key={index}>{day}</li>)}
				</ul>
				<ul className={StylerSessionStyles.days}>
					{currentMonthDates &&
						currentMonthDates.length > 0 &&
						currentMonthDates.map((days, index) => (
							<li key={index} style={{ marginLeft: days.date === 1 ? currentMonthStartDayMargin + '%' : 0 }}>
								{currentMonthNumber === new Date(Date.now()).getMonth() &&
								currentYear === new Date(Date.now()).getFullYear() &&
								currentDate === days.date ? (
									<span className={StylerSessionStyles.active}>{days.date}</span>
								) : (
									days.date
								)}
								{(days.date === dayBeforeYesterdayDate.date &&
									currentMonthNumber === dayBeforeYesterdayDate.month &&
									currentYear === dayBeforeYesterdayDate.year) ||
								(days.date === yesterdayDate.date &&
									currentMonthNumber === yesterdayDate.month &&
									currentYear === yesterdayDate.year) ||
								(days.date === currentDate &&
									currentMonthNumber === new Date(Date.now()).getMonth() &&
									currentYear === new Date(Date.now()).getFullYear()) ? null : (
									getUserInfo &&
									getUserInfo.userInfo &&
									getUserInfo.userInfo.data &&
									getUserInfo.userInfo.data.role === 'trainer' && (
										<svg className={StylerSessionStyles.lockIcon}>
											<use xlinkHref={`/assets/sprite.svg#icon-lock`} />
										</svg>
									)
								)}

								{sessionBoxVisible.visible && sessionBoxVisible.current === days.date ? addStylerSession &&
								addStylerSession.loading ? (
									<p className={StylerSessionStyles.sessionData}>
										<span>•</span> Saving Session...
									</p>
								) : (
									<Fragment>
										<div className={StylerSessionStyles.sessionInputs}>
											<input
												type='text'
												placeholder='eg, 3.5'
												autoFocus={sessionBoxVisible.visible}
												value={sessionText.value}
												onChange={(e) => setSessionText({ value: e.target.value, error: '' })}
												className={StylerSessionStyles.sessionInput}
											/>
											<button
												className={StylerSessionStyles.sessionInputBtn}
												onClick={() => addSession(days.date, days)}>
												<svg className={StylerSessionStyles.sessionInputIcon_check}>
													<use xlinkHref={`/assets/sprite.svg#icon-check`} />
												</svg>
											</button>
											<button
												className={StylerSessionStyles.sessionInputBtn}
												onClick={() => handleAddSession(days.date, '')}>
												<svg className={StylerSessionStyles.sessionInputIcon_close}>
													<use xlinkHref={`/assets/sprite.svg#icon-close`} />
												</svg>
											</button>
										</div>
										{sessionText.error && <p className={StylerSessionStyles.errorText}>{sessionText.error}</p>}
									</Fragment>
								) : loading ? (
									<p className={StylerSessionStyles.sessionData}>
										<span>•</span> Loading...
									</p>
								) : (
									<p
										className={StylerSessionStyles.sessionData}
										onDoubleClick={() => handleAddSession(days.date, days.session)}>
										{(getUserInfo &&
										getUserInfo.userInfo &&
										getUserInfo.userInfo.data &&
										getUserInfo.userInfo.data.role === 'gym' ? (
											<Fragment>
												{days.session === 0 ? (
													<button
														className={StylerSessionStyles.sessionDataAddBtn}
														onClick={() => handleAddSession(days.date, '')}>
														Add Sessions
													</button>
												) : (
													<Fragment>
														<span>•</span> {days.session} sessions
														<button
															className={StylerSessionStyles.sessionDataBtn}
															onClick={() => handleAddSession(days.date, days.session)}>
															<svg className={StylerSessionStyles.sessionDataIcon}>
																<use xlinkHref={`/assets/sprite.svg#icon-edit`} />
															</svg>
														</button>
													</Fragment>
												)}
											</Fragment>
										) : (
											days.date === dayBeforeYesterdayDate.date &&
											currentMonthNumber === dayBeforeYesterdayDate.month &&
											currentYear === dayBeforeYesterdayDate.year
										)) ||
										(days.date === yesterdayDate.date &&
											currentMonthNumber === yesterdayDate.month &&
											currentYear === yesterdayDate.year) ||
										(days.date === currentDate &&
											currentMonthNumber === new Date(Date.now()).getMonth() &&
											currentYear === new Date(Date.now()).getFullYear()) ? (
											<Fragment>
												{days.session === 0 ? (
													<button
														className={StylerSessionStyles.sessionDataAddBtn}
														onClick={() => handleAddSession(days.date, '')}>
														Add Sessions
													</button>
												) : (
													<Fragment>
														<span>•</span> {days.session} sessions
														<button
															className={StylerSessionStyles.sessionDataBtn}
															onClick={() => handleAddSession(days.date, days.session)}>
															<svg className={StylerSessionStyles.sessionDataIcon}>
																<use xlinkHref={`/assets/sprite.svg#icon-edit`} />
															</svg>
														</button>
													</Fragment>
												)}
											</Fragment>
										) : (
											<Fragment>
												<span>•</span> {days.session} sessions
											</Fragment>
										)}
									</p>
								)}
							</li>
						))}
				</ul>

				{getStylerSessions &&
				getStylerSessions.sessions &&
				getStylerSessions.sessions.data &&
				getStylerSessions.sessions.data.totalSessionHr ? (
					<div className={StylerSessionStyles.totalSection}>
						<p className={StylerSessionStyles.totalSectionData}>
							Total Sessions <span>{getStylerSessions.sessions.data.totalSessionHr} Hours</span>
						</p>
						<p className={StylerSessionStyles.totalSectionData}>
							Hours Owed <span>{`$${getStylerSessions.sessions.data.totalSessionHrOwed}`}</span>
						</p>
						{getStylerSessions.sessions.data.utilitiesOwed ? (
							<p className={StylerSessionStyles.totalSectionData}>
								Utilities Owed <span>{`$${getStylerSessions.sessions.data.utilitiesOwed}`}</span>
							</p>
						) : null}
						{getStylerSessions.sessions.data.totalOwed ? (
							<p className={StylerSessionStyles.totalSectionData}>
								Total Owed <span>{`$${getStylerSessions.sessions.data.totalOwed}`}</span>
							</p>
						) : null}
					</div>
				) : (
					<div className={StylerSessionStyles.totalSection}>
						<p className={StylerSessionStyles.totalSectionData}>
							Total Sessions <span>0 Hours</span>
						</p>
						<p className={StylerSessionStyles.totalSectionData}>
							Hours Owed <span>{`$0`}</span>
						</p>
					</div>
				)}
			</div>
		</Content>
	);
};

export default StylerSessionScreen;
