import {
	ATTENDANCE_LIST_FAIL,
	ATTENDANCE_LIST_REQUEST,
	ATTENDANCE_LIST_SUCCESS,
	ATTENDANCE_LIST_RESET,
	CHECKINOUT_REQUEST,
	CHECKINOUT_SUCCESS,
	CHECKINOUT_FAIL,
	CHECKINOUT_RESET,
	DELETE_CHECKIN_REQUEST,
	DELETE_CHECKIN_SUCCESS,
	DELETE_CHECKIN_FAIL,
	DELETE_CHECKIN_RESET,
	GET_STYLER_SESSIONS_REQUEST,
	GET_STYLER_SESSIONS_SUCCESS,
	GET_STYLER_SESSIONS_FAIL,
	GET_STYLER_SESSIONS_RESET,
	ADD_STYLER_SESSION_REQUEST,
	ADD_STYLER_SESSION_SUCCESS,
	ADD_STYLER_SESSION_FAIL,
	ADD_STYLER_SESSION_RESET,
	TOTAL_STYLER_SESSION_REQUEST,
	TOTAL_STYLER_SESSION_SUCCESS,
	TOTAL_STYLER_SESSION_FAIL,
	TOTAL_STYLER_SESSION_RESET
} from '../constants/attendanceConstants';

export const checkInOutReducer = (state = {}, action) => {
	switch (action.type) {
		case CHECKINOUT_REQUEST:
			return { loading: true };

		case CHECKINOUT_SUCCESS:
			return { userInfo: action.payload, loading: false };

		case CHECKINOUT_FAIL:
			return { loading: false, error: action.payload };

		case CHECKINOUT_RESET:
			return {};

		default:
			return state;
	}
};

