import React, { useState, useEffect, Fragment } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Content from '../../components/Content';
import Spinner from '../../components/Spinner/Spinner';
import {
	getEarningsReportByMonthAction,
	getGeneralCountAction,
	getSessionChartDataAction,
	getSessionReportAction,
	getStylerTotalEarningByMonthChartAction,
	getStylerTotalSessionByMonthAction,
	getStylerTotalSessionByMonthChartAction
} from '../../redux/actions/reportsActions';
import { Bar } from 'react-chartjs-2';
import { options } from './chartOptions';
import ReportToggle from './ReportToggle';
import ReportHeader from './ReportHeader';
import Chart from 'chart.js/auto';
import ReportStyles from './Report.module.css';
import { Link } from 'react-router-dom';
import IncomeChart from './IncomeChart';
import ReportHeaderStyler from './ReportHeaderStyler';
import { months } from './months';

const ReportScreen = ({ history }) => {
	const dispatch = useDispatch();

	const [ ischarts, setISCharts ] = useState(false);
	const [ month, setMonth ] = useState(new Date().getMonth() + 1);

	const userLogin = useSelector((state) => state.userLogin);
	const getUserInfo = useSelector((state) => state.getUserInfo);
	const sessionReport = useSelector((state) => state.sessionReport);
	const sessionChartReport = useSelector((state) => state.sessionChartReport);
	const generalCountReport = useSelector((state) => state.generalCountReport);
	const earningReportByMonth = useSelector((state) => state.earningReportByMonth);
	const getStylerSessionByMonth = useSelector((state) => state.getStylerSessionByMonth);
	const getStylerSessionByMonthChart = useSelector((state) => state.getStylerSessionByMonthChart);
	const getStylerEarningByMonthChart = useSelector((state) => state.getStylerEarningByMonthChart);
 
	useEffect(
		() => {
			if (!(userLogin && userLogin.userInfo && userLogin.userInfo.data && userLogin.userInfo.data.token)) {
				history.push('/login');
			} else if (
				getUserInfo &&
				getUserInfo.userInfo &&
				getUserInfo.userInfo.data &&
				(getUserInfo.userInfo.data.role === 'gym' ||
					getUserInfo.userInfo.data.role === 'gymadmin' ||
					getUserInfo.userInfo.data.role === 'superadmin')
			) {
				dispatch(getGeneralCountAction());
				dispatch(getSessionReportAction({ month: month }));
				dispatch(getSessionChartDataAction({ month: month }));
				dispatch(getEarningsReportByMonthAction());
			} else if (
				getUserInfo &&
				getUserInfo.userInfo &&
				getUserInfo.userInfo.data &&
				getUserInfo.userInfo.data.role === 'trainer'
			) {
				dispatch(getStylerTotalSessionByMonthAction());
				dispatch(getStylerTotalSessionByMonthChartAction());
				dispatch(getStylerTotalEarningByMonthChartAction());
			}
		},
		[ history, getUserInfo, dispatch ]
	);

	const onChangeMonthHandler = (m) => {
		setMonth(m);
		dispatch(getSessionReportAction({ month: m }));
		dispatch(getSessionChartDataAction({ month: m }));
	};

	return (
		<Content currentMenu='dashboard' headerTitle='Dashboard' addBtn={false} search={false}>
			{getUserInfo &&
			getUserInfo.userInfo &&
			getUserInfo.userInfo.data &&
			getUserInfo.userInfo.data.role === 'trainer' ? (
				<div className={ReportStyles.reportContent}>
					<div className={ReportStyles.leftContent}>
						{getStylerSessionByMonth && getStylerSessionByMonth.loading ? (
							<Spinner />
						) : (
							// <ReportHeaderStyler getStylerSessionByMonth={getStylerSessionByMonth} />
							<ReportHeader data={{ generalCountReport: getStylerSessionByMonth }} />
						)}
						{getStylerSessionByMonthChart &&
						getStylerSessionByMonthChart.report &&
						getStylerSessionByMonthChart.report.data && (
							<div className={ReportStyles.chartContent}>
								<div className={ReportStyles.chartContentHeader}>
									<h1 className={ReportStyles.chartContentHeading}>Session Chart</h1>
								</div>
								<Bar
									data={getStylerSessionByMonthChart.report.data}
									options={options}
									className={ReportStyles.report_chart_container}
								/>
							</div>
						)}
					</div>
					{getStylerEarningByMonthChart &&
					getStylerEarningByMonthChart.report &&
					getStylerEarningByMonthChart.report.data && (
						<div className={ReportStyles.rightContent2}>
							<h1 className={ReportStyles.chartContentHeading}>Your Earning History</h1>
							{getStylerEarningByMonthChart.report.data.map((res) => (
								<div key={res._id} className={ReportStyles.trainerList}>
									<div className={ReportStyles.trainerListData}>
										<div>
											<h4 className={ReportStyles.trainerListName}>
												{months.length > 0 && months.map((m) => m.month === res.month && m.name)}
											</h4>
											<h6 className={ReportStyles.trainerListSessions}>
												<span>{res.earning}</span> Sessions
											</h6>
											<p className={ReportStyles.trainerListEarning}>{'Total Earning $' + res.total_session}</p>
										</div>
									</div>
								</div>
							))}
						</div>
					)}
				</div>
			) : sessionReport && sessionReport.loading ? (
				<Spinner />
			) : (
				<div className={ReportStyles.reportContent}>
					<div className={ReportStyles.leftContent}>
						<ReportHeader data={{ generalCountReport }} />
						<div className={ReportStyles.chartContent}>
							<div className={ReportStyles.chartContentHeader}>
								<h1 className={ReportStyles.chartContentHeading}>Styler Session Chart</h1>
								<ReportToggle
									showfilter={true}
									data={{ month }}
									onChangeMonthHandler={onChangeMonthHandler}
									onChange={(status) => setISCharts(status)}
								/>
							</div>

							{sessionChartReport &&
							sessionChartReport.report &&
							sessionChartReport.report.data && (
								<Bar
									data={sessionChartReport.report.data}
									options={options}
									className={ReportStyles.report_chart_container}
								/>
							)}
						</div>
						<IncomeChart earningReportByMonth={earningReportByMonth} />
					</div>
					<div className={ReportStyles.rightContent}>
						<h1 className={ReportStyles.chartContentHeading}>Styler Sessions History</h1>
						{sessionReport &&
							sessionReport.report &&
							sessionReport.report.data &&
							sessionReport.report.data.map((res) => (
								<Link
									to={`/trainer-sessions/${res.trainer._id}`}
									key={res.trainer._id}
									className={ReportStyles.trainerList}>
									<div className={ReportStyles.trainerListData}>
										<img src={res.trainer.photo} alt={res.trainer.name} className={ReportStyles.trainerListImage} />
										<div>
											<h4 className={ReportStyles.trainerListName}>{res.trainer.name}</h4>
											<h6 className={ReportStyles.trainerListSessions}>
												<span>{res.session}</span> Sessions
											</h6>
											<p className={ReportStyles.trainerListEarning}>{'Total Earning $' + res.earning}</p>
										</div>
									</div>
									<svg className={ReportStyles.trainerListIcon}>
										<use xlinkHref={`/assets/sprite.svg#icon-chevron-right`} />
									</svg>
								</Link>
							))}
					</div>
				</div>
			)}
		</Content>
	);
};

export default ReportScreen;
