export const getStylerSessionsAction = (month, trainerId) => async (dispatch, getState) => {
	try {
		dispatch({ type: GET_STYLER_SESSIONS_REQUEST });

		const { userLogin: { userInfo } } = getState();

		const config = {
			headers: {
				token: userInfo.data.token,
				'Content-Type': 'application/json'
			}
		};

		let uri;

		if (trainerId) {
			uri = `${BASE_URL}/session/get-session-by-trainer?month=${month}&offset=-330&id=${trainerId}`;
		} else {
			uri = `${BASE_URL}/session/get-session-by-trainer?month=${month}&offset=-330`;
		}

		const { data } = await axios.get(uri, config);

		dispatch({
			type: GET_STYLER_SESSIONS_SUCCESS,
			payload: data
		});
	} catch (err) {
		dispatch({
			type: GET_STYLER_SESSIONS_FAIL,
			payload: err.response && err.response.data.message ? err.response.data.message : err.message
		});
	}
};

