import React, { useState } from 'react';
import ContentStyle from './Content.module.css';
import WebHeader from '../Headers/Header';
import LoginSidebar from '../../../components/LoginSidebar';

const Content = ({ children }) => {
	// const [loginModalClick, setLoginModalClick] = useState(false);
	// const handleLoginModalClick = () => setLoginModalClick(!loginModalClick);


	return (
		<main className={ContentStyle.container}>
			<LoginSidebar />
			<WebHeader />
			<div className={ContentStyle.content}>{children}</div>
		</main>
	);
};

export default Content;
