import React, { useEffect, useState } from "react";
import { NavLink } from 'react-router-dom';
import './Header.css';
import Button from "../../../components/formInputs/Button";

const WebHeader = () => {
	const [click, setClick] = useState(false);
	const handleClick = () => setClick(!click);


	const closeMobileMenu = () => setClick(false);

	const toggleMenu = () => {
		let navigation = document.querySelector('.navigationLoginSidebar');
		navigation.classList.toggle('active');
	};


	return (
		<header className='website_header'>
			<img src='./assets/favicon.png' alt='Logo' className='logo_website' />
			<nav className='navigation_website'>
				<div>
					<ul className={click ? " navigation__links nav-options active" : "navigation__links nav-options"}>
						<li onClick={closeMobileMenu}>
							<NavLink exact to='/login' activeClassName='navigation__active' className='navigation__link'>
								Home
							</NavLink>
						</li>
						<li onClick={closeMobileMenu}>
							<NavLink exact to='/Medical-Licensing-Service' activeClassName='navigation__active' className='navigation__link'>
								Shop
							</NavLink>
						</li>
						<li onClick={closeMobileMenu}>
							<NavLink exact to='/State-Medical-Requirement' activeClassName='navigation__active' className='navigation__link'>
								Services
							</NavLink>
						</li>
						<li onClick={closeMobileMenu}>
							<NavLink exact to='/mlp' activeClassName='navigation__active' className='navigation__link'>
								Hours of Operation
							</NavLink>
						</li>
						<li onClick={closeMobileMenu}>
							<NavLink exact to='/mlp' activeClassName='navigation__active' className='navigation__link'>
								Contact Us
							</NavLink>
						</li>

						<li className="mobile-menu">
							<div className='navigation__link header-right-side'>
								<Button style={{
									width: '25rem',
									justifyContent: 'center',
									backgroundColor: '#ff0000'
								}} label='Book Now' icon='arrow_right' />
							</div>
						</li>
						<li className="mobile-menu">
							<div className='navigation__link header-right-side'>
								<Button style={{
									width: '25rem',
									justifyContent: 'center'
								}} label='Login' icon='arrow_right'
									onClick={toggleMenu}
								/>
							</div>
						</li>
					</ul>
				</div>
				<ul className='navigation__links navigation__links--contact'>
					<li>
						<div className='navigation__link header-right-side'>
							<Button style={{
								width: '25rem',
								justifyContent: 'center',
								backgroundColor: '#ff0000'
							}} label='Book Now' icon='arrow_right' />
						</div>
					</li>
					<li>
						<div className='navigation__link header-right-side'>
							<Button style={{
								width: '25rem',
								justifyContent: 'center'
							}} label='Login' icon='arrow_right'
								onClick={toggleMenu}
							/>
						</div>
					</li>
				</ul>
				<div className="mobile-menu" onClick={handleClick}>
					{click ? (
						<svg className='header-icon-in menu-icon'>
							<use xlinkHref='assets/sprite.svg#CloseMenu' />
						</svg>
					) : (
						<svg className='header-icon-in menu-icon'>
							<use xlinkHref='assets/sprite.svg#MenuIcon' />
						</svg>
					)}
				</div>
			</nav>
		</header>
	);
};

export default WebHeader;
