import React, { useEffect, useState } from 'react';
import Banner from '../components/SimpleBanner/Banner';
import Content from '../components/Content/Content';
import { months, days } from './calendarData';
import './BookAppointment.css';
import StylerSessionStyles from '../../screens/StylerSessionScreen/StylerSession.module.css';
import SelectBox from '../../components/formInputs/SelectBox';


const BookAppointment = ({ history }) => {
	const [currentMonthDates, setCurrentMonthDates] = useState([]);
	const [currentYear, setCurrentYear] = useState('');
	const [currentDate, setCurrentDate] = useState('');
	const [currentMonth, setCurrentMonth] = useState({});
	const [currentMonthNumber, setCurrentMonthNumber] = useState(null);
	const [currentDayName, setCurrentDayName] = useState('');
	const [currentMonthStartDay, setCurrentMonthStartDay] = useState('');
	const [currentMonthStartDayMargin, setCurrentMonthStartDayMargin] = useState(0);


	useEffect(
		() => {
			const date = new Date(Date.now());

			months.map((month, i) => i === currentMonthNumber && setCurrentMonth({ index: 11, name: 'December', days: 31 }));
			setCurrentDate(date.getDate());
			days.map((day, i) => i === date.getDay() && setCurrentDayName(day));

			setCurrentMonthStartDay(new Date(currentYear, currentMonthNumber, 1).getDay());
			setCurrentMonthStartDayMargin(currentMonthStartDay * 13.6);
		},
		[currentMonthNumber, currentYear, currentMonthStartDay]
	);


	useEffect(
		() => {
			const days = [];

			if (currentYear / 4 === 0 && currentMonth && currentMonth.name === 'February') {
				setCurrentMonth({ ...currentMonth, days: 29 });
			}
			if (currentMonth && currentMonth.days) {
				for (let i = 1; i <= currentMonth.days; i++) {
					days.push({ date: i, session: 0 });
				}
			}

			setCurrentMonthDates(days);
		},
		[currentYear, currentMonth]
	);


	return (
		<Content>
			<Banner
				backgroundImage='url(/assets/11357.jpg)'
				title="Red's Barber Lounge"
				subTitle='8770 East Arapahoe Road, Suite 144 in Solera Salon Building, Centennial, Colorado'
				height='50vh'
			/>
			<div className='top_card__full'>
				<div className={StylerSessionStyles.headers} style={{ width: '100%', justifyContent: 'center', gap: '2rem' }}>
					<SelectBox icon="calendar">
						{months &&
							months.map((status) => (
								<option value={status.value} key={status._id}>
									{status.name}
								</option>
							))}
					</SelectBox>
					<SelectBox icon="shopping-basket">
						<option value=''>Select Service</option>
					</SelectBox>
					<SelectBox icon="archive">
						<option value=''>Select Category</option>
					</SelectBox>
				</div>
				<div className={StylerSessionStyles.headers} style={{ width: '100%' }}>
					<button className={StylerSessionStyles.prevNextBtn} >
						&#10094;
					</button>
					<p className={StylerSessionStyles.dayMonth}>
						<ul className='days_list'>
							<li>
								1 <br />
								Mon
							</li>
							<li>
								2 <br />
								Tue
							</li>
							<li>
								3 <br />
								Wed
							</li>
							<li>
								4 <br />
								Thu
							</li>
							<li>
								5 <br />
								Fri
							</li>
							<li>
								6 <br />
								Sat
							</li>
							<li style={{
								backgroundColor: 'black',
								color: 'white',
								padding: '1rem',
								borderRadius: '1rem',
							}}>
								7 <br />
								Sun
							</li>
							<li>
								8 <br />
								Mon
							</li>
							<li>
								9 <br />
								Tue
							</li>
							<li>
								10 <br />
								Wed
							</li>
							<li>
								11 <br />
								Thu
							</li>
							<li>
								12 <br />
								Fri
							</li>

							{/* {currentMonthDates &&
								currentMonthDates.length > 0 &&
								currentMonthDates.map((days, index) => (
									<li key={index}>
										<span className={StylerSessionStyles.active}>{days.date}</span>
									</li>))
							} */}
						</ul>
					</p>
					<button className={StylerSessionStyles.prevNextBtn}>
						&#10095;
					</button>
				</div>

				<h2 className='top_card__full--heading'></h2>


			</div>


			<div className='event_content'>
				<h2 className='top_card__full--heading'>2021 Registered Teams</h2>
			</div>
		</Content>
	);
};

export default BookAppointment;
