import axios from 'axios';
import {
	STYLER_REQUEST,
	STYLER_SUCCESS,
	STYLER_FAIL,
	DELETE_STYLER_SUCCESS,
	DELETE_STYLER_REQUEST,
	DELETE_STYLER_FAIL,
	CREATE_NEW_STYLER_REQUEST,
	CREATE_NEW_STYLER_SUCCESS,
	CREATE_NEW_STYLER_FAIL,
	CHANGE_STATUS_STYLER_REQUEST,
	CHANGE_STATUS_STYLER_SUCCESS,
	CHANGE_STATUS_STYLER_FAIL
} from '../constants/stylerConstants';
import { BASE_URL } from './ip';

export const getStylerListAction = (formData) => async (dispatch, getState) => {
	try {
		dispatch({ type: STYLER_REQUEST });

		const { userLogin: { userInfo } } = getState();

		const config = {
			headers: {
				token: userInfo.data.token,
				'Content-Type': 'application/json'
			}
		};

		const { data } = await axios.get(
			`${BASE_URL}/trainer/get-trainer?pageNumber=${formData.pageNumber}&pageSize=${formData.pageSize}&filterValue=${formData.filter}`,
			config
		);

		dispatch({
			type: STYLER_SUCCESS,
			payload: data
		});
	} catch (err) {
		dispatch({
			type: STYLER_FAIL,
			payload: err.response && err.response.data.message ? err.response.data.message : err.message
		});
	}
};

export const deleteStylerAction = (id) => async (dispatch, getState) => {
	try {
		dispatch({ type: DELETE_STYLER_REQUEST });

		const { userLogin: { userInfo } } = getState();

		const config = {
			headers: {
				token: userInfo.data.token,
				'Content-Type': 'application/json'
			}
		};

		const data = await axios.delete(`${BASE_URL}/trainer/delete-trainer?trainerId=${id}`, config);

		dispatch({
			type: DELETE_STYLER_SUCCESS,
			payload: data
		});
	} catch (err) {
		dispatch({
			type: DELETE_STYLER_FAIL,
			payload: err.response && err.response.data.message ? err.response.data.message : err.message
		});
	}
};

