import {
	STYLER_REQUEST,
	STYLER_SUCCESS,
	STYLER_FAIL,
	STYLER_RESET,

	CREATE_NEW_STYLER_REQUEST,
	CREATE_NEW_STYLER_SUCCESS,
	CREATE_NEW_STYLER_FAIL,
	CREATE_NEW_STYLER_RESET,

	UPDATE_STYLER_REQUEST,
	UPDATE_STYLER_RESET,
	UPDATE_STYLER_FAIL,
	UPDATE_STYLER_SUCCESS,

	DELETE_STYLER_REQUEST,
	DELETE_STYLER_SUCCESS,
	DELETE_STYLER_FAIL,
	DELETE_STYLER_RESET,
	CHANGE_STATUS_STYLER_REQUEST,
	CHANGE_STATUS_STYLER_SUCCESS,
	CHANGE_STATUS_STYLER_FAIL,
	CHANGE_STATUS_STYLER_RESET,
} from '../constants/stylerConstants';


export const getStylerListReducer = (state = {}, action) => {
	switch (action.type) {
		case STYLER_REQUEST:
			return { loading: true };

		case STYLER_SUCCESS:
			return { userInfo: action.payload, loading: false };

		case STYLER_FAIL:
			return { loading: false, error: action.payload };

		case STYLER_RESET:
			return {};

		default:
			return state;
	}
};



